.video {
    background-image: url("../Assets/background.png");
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    filter: blur(2px);
}

.video video {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.video .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}