.background-service {
    justify-content: center; /* горизонтальная центровка */
    align-items: center; /* вертикальная центровка */
    background-size: cover;
}

.main {
    font-size: 3vw;
    font-weight: bold;
    text-align: left;
    color: #FDB10C;
    margin: 0;
    display: grid;
    padding-left: 6%;
    padding-top: 1%;
}

.service {
    display: grid;
    grid-template-columns: 1fr 1fr; /* Две колонки */
    gap: 10vh;
    text-align: end;
    padding: 1% 5% 3%;
}

.block {
    padding: 2vh;
    border: none;
    background-image: none;
    border-radius: 6vh;
    width: 85vh;
    height: 35vh;
    position: relative;
}

.text-block {
    text-align: end;
    color: white;
}

.button-block {
    background-color: #FDB10C;
    border: none;
    color: white;
    text-align: center;
    display: inline-block;
    font-size: 1.1vw;
    font-weight: bolder;
    border-radius: 2vw;
    transition: background-color 0.3s, transform 0.2s;
    cursor: pointer;
    width: 10vw;
    height: 3.5vw;
    margin-top: 5vh;
    margin-left: 10%;
    position: absolute;
    bottom: 4vh;
    right: 2vw;
}

.button-block:hover {
    background-color: #b08118;
}

.button-block:active {
    background-color: rgba(0, 0, 0, 0.47);
}

.button-block:disabled {
    background-color: #cccccc;
    color: #666666;
    cursor: not-allowed;
}

@media (max-width: 768px) {
    .main {
        font-size: 6vw;
        text-align: center;
        padding-left: 0;
        padding-top: 2vh;
    }

    .service {
        grid-template-columns: 1fr; /* Одна колонка */
        gap: 5vh;
        padding: 2% 4%;
    }

    .block {
        width: 100%;
        height: 30vh;
        border-radius: 3vh;
    }

    .text-block {
        font-size: 4vw;
        text-align: center;
        padding: 0 5%;
    }

    .button-block {
        font-size: 4vw;
        width: 40vw;
        height: 8vw;
        border-radius: 5vw;
        right: 50%;
        transform: translateX(50%);
        bottom: 2vh;
        margin-left: 0;
    }
}
